.fd-slider-focused .fd-slider-handle:before,
.fd-slider-hover .fd-slider-handle:before,
.fd-slider-active .fd-slider-handle:before {
  display: block;
  position: absolute;
  top: -21px;
  left: -8px;
  margin: 0;
  width: 20px;
  padding: 3px;
  height: 14px;
  line-height: 12px;
  text-align: center;
  font-size: 10px;
