/*
 * Custom Radio and Input
 * SETUP
 */
.checkbox input[type="checkbox"].checkbox + span,
.checkbox-inline input[type="checkbox"].checkbox + span,
.radiobox-inline input[type="radio"].radiobox + span,
.radio input[type="radio"].radiobox + span {
  margin-left: -20px;
}
.form-inline .checkbox input[type="checkbox"].checkbox + span,
.form-inline .radiobox input[type="radio"].radiobox + span {
  margin-left: 0px;
}
.form-inline .checkbox,
.form-inline .radiobox {
  padding-left: 0px;
}
label input[type="radio"].radiobox + span,
input[type="checkbox"].checkbox + span {
  vertical-align: middle;
  line-height: 20px;
}
input[type="checkbox"].checkbox + span,
input[type="checkbox"].checkbox + span:after,
input[type="checkbox"].checkbox + span:before,
input[type="radio"].radiobox + span,
input[type="radio"].radiobox + span:after,
input[type="radio"].radiobox + span:before {
  box-sizing: content-box !important;
  -webkit-box-sizing: content-box !important;
  -moz-box-sizing: content-box !important;
}
input[type="checkbox"].checkbox,
input[type="radio"].radiobox {
  visibility: hidden;
  position: absolute;
  z-index: 20;
  width: 18px;
  height: 18px;
}
label input[type="checkbox"].checkbox + span,
label input[type="radio"].radiobox + span {
  position: relative;
  z-index: 19;
  display: inline-block;
  margin: 0;
  line-height: 17px;
  min-height: 14px;
  min-width: 14px;
  margin-right: 5px;
}
label input[type="checkbox"].checkbox + span:hover,
label input[type="radio"].radiobox + span:hover {
  cursor: pointer;
}
label input[type="radio"].radiobox + span:before,
label input[type="checkbox"].checkbox + span:before {
  font-family: 'FontAwesome';
  font-size: 12px;
  border-radius: 0;
  content: "\a0";
  /* we use '\a0'; to simulate a non-breaking-space (http://milov.nl/7029) */
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  padding: 1px;
  height: 12px;
  line-height: 12px;
  min-width: 12px;
  margin-right: 5px;
  border: 1px solid #bfbfbf;
  background-color: #f4f4f4;
  font-weight: normal;
  margin-top: -1px;
}
/*
 * RADIO
 */
label input[type="radio"].radiobox + span:before {
  content: "\a0";
  /* we use '\a0'; to simulate a non-breaking-space (http://milov.nl/7029) */
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  box-shadow: inset 0px 2px 3px 0px rgba(0, 0, 0, 0.1), 0px 1px 0px 0px rgba(255, 255, 255, 0.2);
}
label input[type="radio"].radiobox:checked + span {
  font-weight: bold;
}
/* DEFAULT COLOR */
label input[type="radio"].radiobox:checked + span:before {
  content: "\f111";
  /* circle */
  color: #2E7BCC;
}
/* ORANGE COLOR */
label input[type="radio"].radiobox.style-3:checked + span:before {
  color: #a90329;
  border-color: #a90329;
}
/* CHROME COLOR */
