/*
	 * MAIN
	 */
article.sortable-grid {
  min-height: 30px;
}
.jarviswidget {
  margin: 0 0 30px 0;
  position: relative;
  -moz-border-radius: 0px;
  -khtml-border-radius: 0px;
  -webkit-border-radius: 0px;
  border-radius: 0px;
  padding: 0;
}
.widget-content-padding {
  padding: 20px;
}
.widget-content-padding .well {
  margin-bottom: 0px;
}
/*
	 * HEADER
	 */
.jarviswidget > header {
  height: 34px;
  padding: 0;
  line-height: 40px;
}
.jarviswidget > header > .widget-icon {
  display: block;
  width: 30px;
  height: 32px;
  position: relative;
  float: left;
  font-size: 111%;
  line-height: 32px;
  text-align: center;
  margin-right: -10px;
}
.jarviswidget > header h2 {
  height: 100%;
  width: auto;
  display: inline-block;
  font-size: 14px;
  position: relative;
  margin: 0;
  line-height: 34px;
  font-weight: normal;
  letter-spacing: 0px;
}
.jarviswidget > header > h2 {
  margin-left: 10px;
  float: left;
}
.jarviswidget > header > :first-child.widget-icon {
  margin-left: 0;
}
.jarviswidget > header .jarviswidget-icon {
  width: 16px;
  height: 16px;
  float: left;
  margin: 12px 10px 0 0;
  display: block;
  zoom: 1;
  z-index: 4000;
}
.ui-sortable .jarviswidget-sortable > header {
  cursor: move;
  -moz-border-radius: 0px;
  -khtml-border-radius: 0px;
  -webkit-border-radius: 0px;
  border-radius: 0px;
  line-height: normal;
  box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.05);
  -moz-box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.05);
  -webkit-box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.05);
}
.ui-sortable .jarviswidget-sortable.jarviswidget-collapsed > header {
  -moz-border-radius: 0px;
  -khtml-border-radius: 0px;
  -webkit-border-radius: 0px;
  border-radius: 0px;
}
.jarviswidget-ctrls {
  width: auto;
  float: right;
  padding: 0;
  margin: 0;
}
.jarviswidget-ctrls .button-icon {
  min-width: 30px;
  height: 32px;
  float: left;
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
  border-left: 1px solid rgba(0, 0, 0, 0.09);
}
.jarviswidget-ctrls .button-icon:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
.jarviswidget-loader {
  width: 32px;
  height: 32px;
  margin: 0;
  float: right;
  background-repeat: no-repeat;
  background-position: center center;
  display: none;
  text-align: center;
  line-height: 32px;
  font-size: 111%;
}
/*
	 * CONTENT
	 */
.jarviswidget > div {
  float: left;
  width: 100%;
  position: relative;
  font-size: 13px;
  -moz-border-radius: 0px;
  -khtml-border-radius: 0px;
  -webkit-border-radius: 0px;
  border-radius: 0px;
  margin: 0;
  border-width: 1px 1px 2px 1px;
  border-style: solid;
  border-top: none;
  border-right-color: #CCC !important;
  border-bottom-color: #CCC !important;
  border-left-color: #CCC !important;
  padding: 13px 13px 0px;
  overflow: visible;
}
.jarviswidget .widget-body {
  min-height: 100px;
  position: relative;
  padding-bottom: 13px;
}
.jarviswidget .widget-body.widget-hide-overflow {
  overflow: hidden;
}
.widget-toolbar.no-border {
  border-left: none;
