/*
	 * CLEARFIX
	 */
.jarviswidget:before,
.jarviswidget:after,
.jarviswidget > div:before,
.jarviswidget > div:after,
.inner-spacer:before,
.inner-spacer:after,
.jarviswidget-editbox:before,
.jarviswidget-editbox:after,
.clearfix:before,
.clearfix:after {
  content: "\0020";
  display: block;
  height: 0;
  overflow: hidden;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
}
.jarviswidget:after,
.jarviswidget > div:after,
.inner-spacer:after,
.jarviswidget-editbox:after,
.clearfix:after {
  clear: both;
}
